import '../../../App.css';
import * as React from "react"

import Acacia from "../../../data/img/Acacia_mearnsii.png";
import Casuarina from "../../../data/img/Casuarina_equisetifolia .png";
import Eleocharis from "../../../data/img/Eleocharis_interstincta.png";
import Grevillea from "../../../data/img/Grevillea_robusta.png";


import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import { TableHead } from '@mui/material';



const Plants = () => {


    const Item = styled(Paper)(({ theme }) => ({
        backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
        ...theme.typography.body2,
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    }));

    function createData(plan, edib, med) {
        return { plan, edib, med };
    }

    const rowsBoat = [
        createData('Casuarina equisetifolia', '-', 'Les extraits de racines sont utiliss pour le traitement de la dysenterie, de la diarrhe et des maux d\'estomac. Une dcoction des rameaux est utilise pour traiter les gonflements. L\'corce frache est un excellent astringent. Elle est utilise dans le traitement de la diarrhe chronique et de la dysenterie. Une dcoction de l\'corce interne, combine  l\'corce interne de Terminalia catappa, est utilise pour traiter l\'asthme et l\'essoufflement. L\'corce rduite en poudre est utilise pour traiter les boutons sur le visage. La couche de cambium situe sous l\'corce est presse et utilise pour calmer les malades mentaux ou les patients agressifs.'),
        createData('Grevillea robusta', 'Les fleurs sont l\'une des sources les plus riches en nectar. Celui-ci peut tre aspir directement des fleurs, secou dans un bol ou lav dans une petite quantit d\'eau. Le nectar tombe en pluie lorsqu\'on agite les fleurs.', '-'),
        createData('Acacia mearnsii', '-', 'L\'corce est riche en tanins et peut donc tre utilise dans les nombreux cas o les astringents peuvent tre employs, par exemple pour traiter la diarrhe et la dysenterie, pour traiter les hmorrodes, pour arrter les hmorragies internes, pour baigner les coupures et les corchures, comme bain de bouche pour resserrer les dents dans les gencives, etc. L\'corce de toutes les espces d\'Acacia contient des quantits plus ou moins importantes de tanins et est astringente. En application externe, souvent sous forme de bain, elles sont utilises pour traiter les plaies et autres problmes de peau, les hmorrodes, la transpiration des pieds, certains problmes oculaires, comme bain de bouche, etc. De nombreux acacias produisent galement des quantits plus ou moins importantes d\'une gomme provenant du tronc et des tiges. Cette gomme est parfois utilise en usage interne pour traiter la diarrhe et les hmorrodes.'),
    ];



    return (
        <>
            <Typography sx={{ WebkitTextFillColor: "rgb(105, 105, 105) !important", marginBottom:3 }} className="accordion">PATRIMOINE BIOLOGIQUE IDENTIFIABLE  MOINS DE 2 KM DES SITES DE DBARQUEMENT DE KADUTU ET DE BAGIRA</Typography>

            <div style={{ display: "flex", flexDirection: "column" }}>

                <Box sx={{ width: '100%' }}>
                    <Grid container rowSpacing={4} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
                        <Grid item xs={3}>
                            <Item sx={{ height: '100%' }}>
                                <CardMedia
                                    component="img"
                                    alt="Eleocharis"
                                    height="200"
                                    image={Eleocharis}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>PLANTE MERGENTE<br /><i>Eleocharis interstincta</i><br />
                                    </Typography>
                                </CardContent>
                            </Item>
                        </Grid>
                        <Grid item xs={3}>
                            <Item sx={{ height: '100%' }}>
                                <CardMedia
                                    component="img"
                                    alt="Casuarina"
                                    height="200"
                                    image={Casuarina}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>PLANTE ORNEMENTALE ET MDICINALE<br /><i>Casuarina equisetifolia</i><br />
                                    </Typography>
                                </CardContent>
                            </Item>
                        </Grid>
                        <Grid item xs={3}>
                            <Item sx={{ height: '100%' }}>
                                <CardMedia
                                    component="img"
                                    alt="Grevillea"
                                    height="200"
                                    image={Grevillea}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>PLANTE DE FORT<br /><i>Grevillea robusta</i><br />
                                    </Typography>
                                </CardContent>
                            </Item>
                        </Grid>
                        <Grid item xs={3}>
                            <Item sx={{ height: '100%' }}>
                                <CardMedia
                                    component="img"
                                    alt="Acacia"
                                    height="200"
                                    image={Acacia}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>PLANTE DE FORT<br /><i>Acacia mearnsii</i><br />
                                    </Typography>
                                </CardContent>
                            </Item>
                        </Grid>
                    </Grid>
                </Box>

                <Accordion defaultExpanded className="pirogueaccordion" style={{ width: "100%", padding: 0, marginTop: 30 }}>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography sx={{ WebkitTextFillColor: "rgb(105, 105, 105) !important" }} className="accordion"></Typography>
                    </AccordionSummary>
                    <AccordionDetails>

                        <TableContainer className="pirogueTableContainer" component={Paper} sx={{ maxHeight: 200, overflow: 'scroll' }}>
                            <Table className="pirogueTable" sx={{ minWidth: 200, fontSize: 15 }} aria-label="simple table">
                                <TableHead>
                                    <TableRow >
                                        <TableCell>NOM SCIENTIFIQUE</TableCell>
                                        <TableCell align="center">UTILISATION DES PLANTES COMESTIBLES</TableCell>
                                        <TableCell align="center">USAGE MDICAL</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {rowsBoat.map((row) => (
                                        <TableRow
                                            key={rowsBoat.item}
                                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                        >
                                            <TableCell component="th" scope="row" sx={{ fontSize: "11px !important" }}>
                                                {row.plan}
                                            </TableCell>
                                            <TableCell align="center" sx={{ fontSize: "11px !important" }}>{row.edib}</TableCell>
                                            <TableCell align="center" sx={{ fontSize: "11px !important" }}>{row.med}</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </AccordionDetails>
                </Accordion>

            </div>

        </>
    )
}


export default Plants;