import '../../../App.css';
import * as React from "react"

import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import { Modal } from '@mui/material';
import Backdrop from '@mui/material/Backdrop';

import Cynthiaimg from "../../../data/img/faces_2.png";
import Mutomboimg from "../../../data/img/faces_3.png";
import Bintuimg from "../../../data/img/faces_4.png";




const Facesb = () => {


    const Item = styled(Paper)(({ theme }) => ({
        backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
        ...theme.typography.body2,
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    }));

    const [open1, setOpen1] = React.useState(false);
    const [open2, setOpen2] = React.useState(false);
    const [open3, setOpen3] = React.useState(false);


    const stylemodal = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 380,
        bgcolor: 'background.paper',
        border: '1px solid rgba(146, 145, 145, 0.4)',
        boxShadow: 0,
        p: 2,
    };


    return (
        <>
            <Box sx={{ width: '100%' }}>
                <Grid container rowSpacing={3} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>

                    <Grid item xs={4}>
                        <Item sx={{ height: '100%', outline: "none" }}>
                            <Button onClick={() => setOpen1(true)} className='boatmodal'>
                                <CardMedia
                                    component="img"
                                    alt="Maman CYNTHIA"
                                    height="300"
                                    image={Cynthiaimg}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" style={{ paddingTop: 10 }} gutterBottom><span style={{ fontWeight: 200, fontSize: "2.3em", letterSpacing: 3, lineHeight: "1.1em" }}>Maman CYNTHIA</span><br />g de 43 ans, mre de 14ans<br />Vingt-cinq ans de pche<br /> KADUTU
                                    </Typography>
                                </CardContent>
                            </Button>
                            <Modal
                                aria-labelledby="transition-modal-title"
                                aria-describedby="transition-modal-description"
                                open={open1}
                                onClose={() => setOpen1(false)}
                                closeAfterTransition
                                slots={{ backdrop: Backdrop }}
                                slotProps={{
                                    backdrop: {
                                        timeout: 500,
                                    },
                                }}
                            >
                                <Box className="boatboxmodal" sx={stylemodal} style={{ backgroundColor: "rgba(247, 245, 251, 1)" }}>
                                    <CardMedia
                                        component="img"
                                        alt="Maman CYNTHIA"
                                        height="300"
                                        image={Cynthiaimg}
                                        sx={{ objectFit: "contain" }} />
                                    <CardContent>
                                        <Typography component='div' className="boatdescr" color="text.secondary" gutterBottom>Je suis  la premire femme  devenir pcheuse dans mon petit village ctier du Sud-Kivu. Ouvrant la voie  dautres femmes, jai plus de 25 ans dans ce mtier, jtais inspire et forme par mon  mari. Je suis une mre de 14 enfants parmi lesquels 9 issus du mariage de mon feu mari. Jai du mal  les scolariser suite  cette vie de pche. Quand mon mari est mort  en faisant ce travail qui nourrissait toute la famille, je me suis lanc  dans lespoir dy trouver une vie meilleure, je me suis retrouve sans aucun soutien financier. Je viens dune communaut traditionnelle de pcheurs, Ici, ce sont les hommes qui vont en mer et prennent les dcisions importantes.  Alors je me suis lance. Jtais trs motive parce que je voyais que mon mari pcheur gagnait plus dargent avant sa mort. Les hommes de ma communaut disaient que je ne pouvais pas pcher car je suis une femme et que le poisson ne mordrait pas  lhameon dune femme qui a ses rgles. Je leur ai rpondu que jtais dj mnopause et quils navaient donc pas  sinquiter de a. Un jour jai dcid dessayer de manier le filet.<br />
                                        </Typography>
                                    </CardContent>
                                </Box>
                            </Modal>
                        </Item>
                    </Grid>


                    <Grid item xs={4}>
                        <Item sx={{ height: '100%', outline: "none" }}>
                            <Button onClick={() => setOpen2(true)} className='boatmodal'>
                                <CardMedia
                                    component="img"
                                    alt="Maman Mutombo"
                                    height="300"
                                    image={Mutomboimg}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" style={{ paddingTop: 10 }} gutterBottom><span style={{ fontWeight: 200, fontSize: "2.3em", letterSpacing: 3, lineHeight: "1.1em" }}>Maman MUTOMBO</span><br />g de 35 ans, mre de 8ans<br />Dix ans de pche<br /> KADUTU
                                    </Typography>
                                </CardContent>
                            </Button>
                            <Modal
                                aria-labelledby="transition-modal-title"
                                aria-describedby="transition-modal-description"
                                open={open2}
                                onClose={() => setOpen2(false)}
                                closeAfterTransition
                                slots={{ backdrop: Backdrop }}
                                slotProps={{
                                    backdrop: {
                                        timeout: 500,
                                    },
                                }}
                            >
                                <Box className="boatboxmodal" sx={stylemodal} style={{ backgroundColor: "rgba(247, 245, 251, 1)" }}>
                                    <CardMedia
                                        component="img"
                                        alt="Maman Mutombo"
                                        height="300"
                                        image={Mutomboimg}
                                        sx={{ objectFit: "contain" }} />
                                    <CardContent>
                                        <Typography component='div' className="boatdescr" color="text.secondary" gutterBottom>Lorsque mon mari est mort, j'ai dcid de me lancer dans la pche. Je vendais de petits divers mais cela ne me permettait pas de nourrir mes huit enfants et de les scolariser. Maman Cynthia m'a montr comment une femme peut subvenir aux besoins de sa famille malgr la mort ou l'abandon de son mari, sans pour cela livrer son corps  la dbauche. Je l'ai fait avec courage, force et sans honte. A la mort de mon mari, j'ai d rendre les filets  sa famille. Aujourd'hui, je les loue pour pcher, car je n'ai pas les moyens d'en acheter de meilleurs.<br />
                                        </Typography>
                                    </CardContent>
                                </Box>
                            </Modal>
                        </Item>
                    </Grid>


                    <Grid item xs={4}>
                        <Item sx={{ height: '100%', outline: "none" }}>
                            <Button onClick={() => setOpen3(true)} className='boatmodal'>
                                <CardMedia
                                    component="img"
                                    alt="Maman Bintu"
                                    height="300"
                                    image={Bintuimg}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" style={{ paddingTop: 10 }} gutterBottom><span style={{ fontWeight: 200, fontSize: "2.3em", letterSpacing: 3, lineHeight: "1.1em" }}>Madame BINTU</span><br />g de 25 ans, mre de 2ans<br />Trois ans de pche<br />  KADUTU
                                    </Typography>
                                </CardContent>
                            </Button>
                            <Modal
                                aria-labelledby="transition-modal-title"
                                aria-describedby="transition-modal-description"
                                open={open3}
                                onClose={() => setOpen3(false)}
                                closeAfterTransition
                                slots={{ backdrop: Backdrop }}
                                slotProps={{
                                    backdrop: {
                                        timeout: 500,
                                    },
                                }}
                            >
                                <Box className="boatboxmodal" sx={stylemodal} style={{ backgroundColor: "rgba(247, 245, 251, 1)" }}>
                                    <CardMedia
                                        component="img"
                                        alt="Maman Bintu"
                                        height="300"
                                        image={Bintuimg}
                                        sx={{ objectFit: "contain" }} />
                                    <CardContent>
                                        <Typography component='div' className="boatdescr" color="text.secondary" gutterBottom>Mon mari nous a abandonns, moi et mes deux enfants. Mes parents sont tous deux morts. J'ai choisi de pcher pour ne pas avoir  me prostituer. Je peux ainsi nourrir mes enfants et mes deux jeunes surs, mais je n'ai pas les moyens de les scolariser.<br />
                                        </Typography>
                                    </CardContent>
                                </Box>
                            </Modal>
                        </Item>
                    </Grid>

                </Grid>
            </Box></>
    )
}


export default Facesb;