import '../../App.css';
import React, { Component } from "react";
import { Link } from 'react-router-dom';
import { useLayoutEffect } from 'react';

import Cartecarte from './components/carte';
import Cartemob from './components/carte_b';

import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';


const FRcarte = () => {

    useLayoutEffect(() => {
        window.scrollTo(0, 0)
    });

    return (
        <>

            <Typography component='div' className="title_5" /* style={{fontSize:"0.8rem !important" }} */><br />
                <Link to="/fr-CD" ><br /> Retour  la page d'accueil</Link><br />
            </Typography>

            <CardContent sx={{ maxWidth: 800 }}>
                <Typography component='div' className="agathe" style={{ textTransform: "uppercase" }}>La carte du lac Kivu d'aprs les pcheuses<br /><br />
                </Typography>
            </CardContent>



            <CardContent sx={{ maxWidth: 800 }}>
                <Typography component='div' className="title_3"><br /><br />Nous nous sommes runis pour dessiner une carte de notre terre ancestrale et des eaux sur lesquelles nous naviguons. Notre objectif tait de comprendre quelles sont les adresses les plus importantes pour nous, o sont concentres les ressources dont dpend notre survie, et comment elles sont relies et loignes les unes des autres<br />
                </Typography>
            </CardContent>

            <CardContent sx={{ maxWidth: 800 }}>
                <Typography component='div' className="mutombodescr"><br />Le rsultat visible est un rseau de zones de pche ancestrales insinu entre les les et les baies du bassin sud du lac Kivu, ponctu par les marchs auxquels nous vendons notre poisson. Autour de nous, nous observons les immenses forts qui couvrent les collines du bassin du Congo, d'o mergent des volcans, lments naturels respects qui nous permettent de nous orienter<br />
                </Typography>
            </CardContent>

            <div className='cartedesktop'>
                <Cartecarte />
            </div>

            <div className='cartemobile'>
                <Cartemob />
            </div>
        </>
    )
}


export default FRcarte;